@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@700&display=swap);
html, body {
  overflow: hidden;
  font-size: 14px;
}

body {
  background-color: #a2d5d8;
}

* {
  margin: 0;
  padding: 0;
}

ul {
  list-style-type: none;
}
.app{display:flex;height:100vh;overflow-y:hidden;max-width:1100px;margin:0 auto;font-family:'Quicksand', sans-serif}

.months{display:flex;align-items:center;background:#fff;margin:30px 10px;box-shadow:0px 0px 10px 5px grey}.circle-container{position:relative;width:30em;height:30em;border-radius:50%;padding:0;margin:0vh auto 0;border:solid 5px tomato}.circle-container>*{display:block;position:absolute;top:50%;left:50%;margin:-4em;width:8em;height:8em}.circle-container>*:nth-of-type(1){transform:rotate(0deg) translate(15em) rotate(0deg)}.circle-container>*:nth-of-type(2){transform:rotate(27.69231deg) translate(15em) rotate(-27.69231deg)}.circle-container>*:nth-of-type(3){transform:rotate(55.38462deg) translate(15em) rotate(-55.38462deg)}.circle-container>*:nth-of-type(4){transform:rotate(83.07692deg) translate(15em) rotate(-83.07692deg)}.circle-container>*:nth-of-type(5){transform:rotate(110.76923deg) translate(15em) rotate(-110.76923deg)}.circle-container>*:nth-of-type(6){transform:rotate(138.46154deg) translate(15em) rotate(-138.46154deg)}.circle-container>*:nth-of-type(7){transform:rotate(166.15385deg) translate(15em) rotate(-166.15385deg)}.circle-container>*:nth-of-type(8){transform:rotate(193.84615deg) translate(15em) rotate(-193.84615deg)}.circle-container>*:nth-of-type(9){transform:rotate(221.53846deg) translate(15em) rotate(-221.53846deg)}.circle-container>*:nth-of-type(10){transform:rotate(249.23077deg) translate(15em) rotate(-249.23077deg)}.circle-container>*:nth-of-type(11){transform:rotate(276.92308deg) translate(15em) rotate(-276.92308deg)}.circle-container>*:nth-of-type(12){transform:rotate(304.61538deg) translate(15em) rotate(-304.61538deg)}.circle-container>*:nth-of-type(13){transform:rotate(332.30769deg) translate(15em) rotate(-332.30769deg)}.circle-container div{font-size:18px;display:block;display:flex;justify-content:center;align-items:center;background-color:blue;max-width:100%;padding:40px;border-radius:50%;color:white;border:solid 5px orangered;transition:0.15s}.circle-container div.start{background-color:black}.circle-container div.green{background-color:#78a85d;box-shadow:0 5px 10px #78a85d}.circle-container div.grey{background-color:#dddcdc;box-shadow:0 5px 10px #dddcdc}.circle-container div.blue{background-color:#274684;box-shadow:0 5px 10px #274684}.circle-container div.red{background-color:#8b0333;box-shadow:0 5px 10px #8b0333}.circle-container div:hover{color:orangered}.box{margin:10px 100px}

.users{flex-grow:1;text-align:center;display:flex}.users__container{position:relative;flex-grow:1;margin:30px 20px;background-color:#fee970;border:4px solid orangered;box-shadow:0px 0px 10px 5px orangered}.users__title{background-color:#fff;padding:10px;font-size:18px;margin:0px 0px 20px 0px}.users__hint{position:absolute;color:orangered;top:50%;left:50%;font-size:32px;transform:translate(-50%, -50%)}@-webkit-keyframes bounce{0%{transform:translateY(0)}50%{transform:translateY(10px)}100%{transform:translateY(0)}}@keyframes bounce{0%{transform:translateY(0)}50%{transform:translateY(10px)}100%{transform:translateY(0)}}

.user{padding:3px;margin:3px 40px;border-radius:15px;background-color:white;border:2px solid black}

